import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { FiUser, FiLock, FiBell } from 'react-icons/fi';

const tabData = [
  {
    id: 'account',
    label: 'Account',
    icon: FiUser,
  },
  {
    id: 'security',
    label: 'Security',
    icon: FiLock,
  },
  {
    id: 'notifications',
    label: 'Notifications',
    icon: FiBell,
  },
];

const Settings = () => {
  const [activeTab, setActiveTab] = useState('account');
  const [formData, setFormData] = useState({
    account: {
      fullName: '',
      email: '',
      bio: '',
      language: 'en',
    },
    security: {
      currentPassword: '',
      newPassword: '',
      confirmPassword: '',
      twoFactorEnabled: false,
    },
    notifications: {
      emailNotifications: true,
      pushNotifications: false,
      marketingEmails: false,
      activityDigest: 'daily',
    },
  });

  const [errors, setErrors] = useState({
    account: {},
    security: {},
    notifications: {},
  });

  const handleInputChange = (tab, field, value) => {
    setFormData(prev => ({
      ...prev,
      [tab]: {
        ...prev[tab],
        [field]: value,
      },
    }));

    // Clear error when user starts typing
    if (errors[tab][field]) {
      setErrors(prev => ({
        ...prev,
        [tab]: {
          ...prev[tab],
          [field]: '',
        },
      }));
    }
  };

  const validateForm = (tab) => {
    const newErrors = { ...errors };
    let isValid = true;

    switch (tab) {
      case 'account':
        if (!formData.account.fullName.trim()) {
          newErrors.account.fullName = 'Full name is required';
          isValid = false;
        }
        if (!formData.account.email.trim()) {
          newErrors.account.email = 'Email is required';
          isValid = false;
        } else if (!/\S+@\S+\.\S+/.test(formData.account.email)) {
          newErrors.account.email = 'Invalid email format';
          isValid = false;
        }
        break;

      case 'security':
        if (formData.security.newPassword) {
          if (formData.security.newPassword.length < 8) {
            newErrors.security.newPassword = 'Password must be at least 8 characters';
            isValid = false;
          }
          if (formData.security.newPassword !== formData.security.confirmPassword) {
            newErrors.security.confirmPassword = 'Passwords do not match';
            isValid = false;
          }
        }
        break;

      default:
        break;
    }

    setErrors(newErrors);
    return isValid;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validateForm(activeTab)) {
      // Handle form submission
      console.log('Form data:', formData[activeTab]);
    }
  };

  const TabContent = ({ tab }) => {
    const variants = {
      enter: { x: 20, opacity: 0 },
      center: { x: 0, opacity: 1 },
      exit: { x: -20, opacity: 0 },
    };

    switch (tab) {
      case 'account':
        return (
          <motion.div
            variants={variants}
            initial="enter"
            animate="center"
            exit="exit"
            transition={{ duration: 0.3 }}
          >
            <form onSubmit={handleSubmit} className="space-y-6">
              <div>
                <label className="block text-sm font-medium text-gray-700">Full Name</label>
                <input
                  type="text"
                  value={formData.account.fullName}
                  onChange={(e) => handleInputChange('account', 'fullName', e.target.value)}
                  className={`mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 ${
                    errors.account.fullName ? 'border-red-500' : ''
                  }`}
                />
                {errors.account.fullName && (
                  <p className="mt-1 text-sm text-red-600">{errors.account.fullName}</p>
                )}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700">Email</label>
                <input
                  type="email"
                  value={formData.account.email}
                  onChange={(e) => handleInputChange('account', 'email', e.target.value)}
                  className={`mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 ${
                    errors.account.email ? 'border-red-500' : ''
                  }`}
                />
                {errors.account.email && (
                  <p className="mt-1 text-sm text-red-600">{errors.account.email}</p>
                )}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700">Bio</label>
                <textarea
                  value={formData.account.bio}
                  onChange={(e) => handleInputChange('account', 'bio', e.target.value)}
                  rows={4}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700">Language</label>
                <select
                  value={formData.account.language}
                  onChange={(e) => handleInputChange('account', 'language', e.target.value)}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500"
                >
                  <option value="en">English</option>
                  <option value="es">Spanish</option>
                  <option value="fr">French</option>
                </select>
              </div>

              <div className="flex justify-end">
                <button
                  type="submit"
                  className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
                >
                  Save Changes
                </button>
              </div>
            </form>
          </motion.div>
        );

      case 'security':
        return (
          <motion.div
            variants={variants}
            initial="enter"
            animate="center"
            exit="exit"
            transition={{ duration: 0.3 }}
          >
            <form onSubmit={handleSubmit} className="space-y-6">
              <div>
                <label className="block text-sm font-medium text-gray-700">
                  Current Password
                </label>
                <input
                  type="password"
                  value={formData.security.currentPassword}
                  onChange={(e) =>
                    handleInputChange('security', 'currentPassword', e.target.value)
                  }
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700">
                  New Password
                </label>
                <input
                  type="password"
                  value={formData.security.newPassword}
                  onChange={(e) =>
                    handleInputChange('security', 'newPassword', e.target.value)
                  }
                  className={`mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 ${
                    errors.security.newPassword ? 'border-red-500' : ''
                  }`}
                />
                {errors.security.newPassword && (
                  <p className="mt-1 text-sm text-red-600">
                    {errors.security.newPassword}
                  </p>
                )}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700">
                  Confirm New Password
                </label>
                <input
                  type="password"
                  value={formData.security.confirmPassword}
                  onChange={(e) =>
                    handleInputChange('security', 'confirmPassword', e.target.value)
                  }
                  className={`mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 ${
                    errors.security.confirmPassword ? 'border-red-500' : ''
                  }`}
                />
                {errors.security.confirmPassword && (
                  <p className="mt-1 text-sm text-red-600">
                    {errors.security.confirmPassword}
                  </p>
                )}
              </div>

              <div className="flex items-center">
                <input
                  type="checkbox"
                  id="twoFactor"
                  checked={formData.security.twoFactorEnabled}
                  onChange={(e) =>
                    handleInputChange('security', 'twoFactorEnabled', e.target.checked)
                  }
                  className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                />
                <label htmlFor="twoFactor" className="ml-2 block text-sm text-gray-900">
                  Enable Two-Factor Authentication
                </label>
              </div>

              <div className="flex justify-end">
                <button
                  type="submit"
                  className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
                >
                  Update Security Settings
                </button>
              </div>
            </form>
          </motion.div>
        );

      case 'notifications':
        return (
          <motion.div
            variants={variants}
            initial="enter"
            animate="center"
            exit="exit"
            transition={{ duration: 0.3 }}
          >
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="space-y-4">
                <div className="flex items-center justify-between">
                  <div className="flex items-center">
                    <input
                      type="checkbox"
                      id="emailNotifications"
                      checked={formData.notifications.emailNotifications}
                      onChange={(e) =>
                        handleInputChange(
                          'notifications',
                          'emailNotifications',
                          e.target.checked
                        )
                      }
                      className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                    />
                    <label
                      htmlFor="emailNotifications"
                      className="ml-2 block text-sm text-gray-900"
                    >
                      Email Notifications
                    </label>
                  </div>
                </div>

                <div className="flex items-center justify-between">
                  <div className="flex items-center">
                    <input
                      type="checkbox"
                      id="pushNotifications"
                      checked={formData.notifications.pushNotifications}
                      onChange={(e) =>
                        handleInputChange(
                          'notifications',
                          'pushNotifications',
                          e.target.checked
                        )
                      }
                      className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                    />
                    <label
                      htmlFor="pushNotifications"
                      className="ml-2 block text-sm text-gray-900"
                    >
                      Push Notifications
                    </label>
                  </div>
                </div>

                <div className="flex items-center justify-between">
                  <div className="flex items-center">
                    <input
                      type="checkbox"
                      id="marketingEmails"
                      checked={formData.notifications.marketingEmails}
                      onChange={(e) =>
                        handleInputChange(
                          'notifications',
                          'marketingEmails',
                          e.target.checked
                        )
                      }
                      className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                    />
                    <label
                      htmlFor="marketingEmails"
                      className="ml-2 block text-sm text-gray-900"
                    >
                      Marketing Emails
                    </label>
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700">
                    Activity Digest
                  </label>
                  <select
                    value={formData.notifications.activityDigest}
                    onChange={(e) =>
                      handleInputChange('notifications', 'activityDigest', e.target.value)
                    }
                    className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500"
                  >
                    <option value="daily">Daily</option>
                    <option value="weekly">Weekly</option>
                    <option value="monthly">Monthly</option>
                    <option value="never">Never</option>
                  </select>
                </div>
              </div>

              <div className="flex justify-end">
                <button
                  type="submit"
                  className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
                >
                  Save Preferences
                </button>
              </div>
            </form>
          </motion.div>
        );

      default:
        return null;
    }
  };

  return (
    <div className="max-w-4xl mx-auto py-6 sm:px-6 lg:px-8">
      <div className="bg-white shadow rounded-lg">
        <div className="p-6">
          <h1 className="text-2xl font-bold text-gray-900 mb-8">Settings</h1>

          {/* Tabs */}
          <div className="border-b border-gray-200">
            <nav className="-mb-px flex space-x-8">
              {tabData.map((tab) => {
                const Icon = tab.icon;
                return (
                  <button
                    key={tab.id}
                    onClick={() => setActiveTab(tab.id)}
                    className={`
                      group inline-flex items-center py-4 px-1 border-b-2 font-medium text-sm
                      ${
                        activeTab === tab.id
                          ? 'border-blue-500 text-blue-600'
                          : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                      }
                    `}
                  >
                    <Icon
                      className={`
                        -ml-0.5 mr-2 h-5 w-5
                        ${
                          activeTab === tab.id
                            ? 'text-blue-500'
                            : 'text-gray-400 group-hover:text-gray-500'
                        }
                      `}
                    />
                    {tab.label}
                  </button>
                );
              })}
            </nav>
          </div>

          {/* Tab Content */}
          <div className="mt-8">
            <AnimatePresence mode="wait">
              <TabContent key={activeTab} tab={activeTab} />
            </AnimatePresence>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Settings; 